___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0px;
  margin: 0px;
  font-family: Arial, Helvetica, sans-serif;
}

body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    background-image: linear-gradient(to right, #0011ff, #00e1ff);
    color: white;
}

header {
    width: 100%;
    padding: 12px;
    display: flex;
    justify-content: space-between;
}

.cel-far {
    display: inline-flex;
    overflow: hidden;
    border-radius: 16px;
    box-shadow: 0px 0px 5px black;
    margin: 2px 14px;
}

.cel-far input {
    display: none;
}

.cel-far label {
    padding: 10px 16px;
    font-size: 1.2rem;
    font-weight: bold;
    cursor: pointer;
    transition: background 0.1s;
}

#celsius + label {
    border-right: 1px solid rgb(32, 30, 30);
}

.cel-far input:checked + label {
    background: rgba(0, 0, 0, 0.112);
}

.search-box {
    margin: 2px 14px;
}

.search-box-input {
    border: none;
    outline: none;
    padding: 4px 8px;
    border-radius: 12px;
    background: rgba(128, 128, 128, 0.313);
    color: white;
    font-size: 1.2rem;
}

.search-box-input::placeholder {
    color: white;
    font-size: 1.2rem;
}


.content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.title {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: 3rem;
}

.weather {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 32px;
}

.temp {
    font-size: 8rem;
    font-weight: bold;
}

.divider {  
    border: 2px solid white;
    height: 100px;
}

.extras {
    font-size: 1.5rem;
    font-weight: bold;
}`, "",{"version":3,"sources":["webpack://./src/layout.css"],"names":[],"mappings":"AAAA;EACE,YAAY;EACZ,WAAW;EACX,yCAAyC;AAC3C;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,2BAA2B;IAC3B,WAAW;IACX,aAAa;IACb,gBAAgB;IAChB,6DAA6D;IAC7D,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,oBAAoB;IACpB,gBAAgB;IAChB,mBAAmB;IACnB,6BAA6B;IAC7B,gBAAgB;AACpB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,iBAAiB;IACjB,eAAe;IACf,2BAA2B;AAC/B;;AAEA;IACI,uCAAuC;AAC3C;;AAEA;IACI,gCAAgC;AACpC;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,gBAAgB;IAChB,mBAAmB;IACnB,sCAAsC;IACtC,YAAY;IACZ,iBAAiB;AACrB;;AAEA;IACI,YAAY;IACZ,iBAAiB;AACrB;;;AAGA;IACI,OAAO;IACP,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,2BAA2B;IAC3B,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,eAAe;IACf,iBAAiB;AACrB;;AAEA;IACI,uBAAuB;IACvB,aAAa;AACjB;;AAEA;IACI,iBAAiB;IACjB,iBAAiB;AACrB","sourcesContent":["* {\n  padding: 0px;\n  margin: 0px;\n  font-family: Arial, Helvetica, sans-serif;\n}\n\nbody {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: flex-start;\n    width: 100%;\n    height: 100vh;\n    overflow: hidden;\n    background-image: linear-gradient(to right, #0011ff, #00e1ff);\n    color: white;\n}\n\nheader {\n    width: 100%;\n    padding: 12px;\n    display: flex;\n    justify-content: space-between;\n}\n\n.cel-far {\n    display: inline-flex;\n    overflow: hidden;\n    border-radius: 16px;\n    box-shadow: 0px 0px 5px black;\n    margin: 2px 14px;\n}\n\n.cel-far input {\n    display: none;\n}\n\n.cel-far label {\n    padding: 10px 16px;\n    font-size: 1.2rem;\n    font-weight: bold;\n    cursor: pointer;\n    transition: background 0.1s;\n}\n\n#celsius + label {\n    border-right: 1px solid rgb(32, 30, 30);\n}\n\n.cel-far input:checked + label {\n    background: rgba(0, 0, 0, 0.112);\n}\n\n.search-box {\n    margin: 2px 14px;\n}\n\n.search-box-